body {
  list-style: none;
  background: #f3f02b;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.App {
  text-align: center;
  min-height: 100%;
  position: relative;
  width: 100vw;
  height: 100vh; }

.appTitle {
  padding: 20px;
  font-size: 40px;
  font-family: 'Righteous', cursive;
  color: red;
  width: 70%;
  margin: auto; }
  @media all and (min-width: 780px) {
    .appTitle {
      font-size: 60px; } }

.containerLoadingPikachu {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }

.headerContainer {
  width: 100vw;
  position: relative; }

.pikachuLeftEar {
  border-right: 50px solid black;
  right: 0;
  width: 0;
  height: 0;
  border-bottom: 50px solid transparent;
  position: fixed;
  top: 0; }

.pikachuRightEar {
  border-left: 50px solid black;
  left: 0;
  width: 0;
  height: 0;
  border-bottom: 50px solid transparent;
  position: fixed;
  top: 0; }

.inputFilter {
  width: 60%;
  max-width: 400px;
  height: 30px;
  font-size: 16px;
  border-radius: 5px; }

.pokemonContainer {
  margin: 80px; }

.pokemonList {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center; }

.pokemonCard {
  width: 200px;
  border: 1px solid grey;
  margin: 10px;
  box-shadow: 5px 5px 5px #888888;
  transition: scale ease 1s; }
  .pokemonCard:hover {
    transform: scale(1.1); }

.linkToDetail {
  text-decoration: none;
  color: black; }

.noPokemonContainer {
  height: 80%; }

.catchPokemon {
  width: 240px;
  height: 200px;
  margin-top: 10%; }
  @media all and (max-width: 768px) {
    .catchPokemon {
      width: 180px;
      height: 140px;
      margin-top: 20%; } }

.noPokemonMessage {
  margin: 10%;
  margin-top: 60px;
  margin-bottom: 0px;
  font-family: 'Courier New', Courier, monospace; }
  @media all and (max-width: 768px) {
    .noPokemonMessage {
      font-size: 24px;
      margin-top: 20%; } }

.noPokemonMessage2 {
  font-family: 'Courier New', Courier, monospace;
  font-size: 40px;
  color: red;
  font-weight: 700; }

.footerContainer {
  width: 100vw;
  height: 80px;
  position: fixed;
  bottom: 0px; }

.flexFooterContainer {
  width: 100%;
  height: 80px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: yellow;
  box-shadow: 0px 0px 20px 0px  #888888; }

.pikachuCheekLeft {
  background-color: red;
  height: 80px;
  margin: 0;
  width: 80px;
  border-top-right-radius: 100%; }

.pikachuCheekRight {
  background-color: red;
  height: 80px;
  margin: 0;
  width: 80px;
  border-top-left-radius: 100%; }

.imageIdContainer {
  background-color: lightgray;
  display: flex;
  flex-direction: column;
  align-items: center; }

.imagePokemon {
  width: 100px; }

.idContainer {
  background-color: grey;
  width: 35%;
  align-self: flex-start;
  height: 25%; }

.pokemonId {
  margin: 0; }

.nameTypeContainer {
  background-color: white;
  text-transform: capitalize;
  text-align: start;
  padding: 10px; }

.pokemonName {
  margin: 0;
  padding: 10px;
  font-weight: 700; }

.pokemonTypeList {
  display: flex; }

.pokemonTypeList p {
  font-weight: 400;
  text-align: center;
  margin: 5px;
  padding: 3px;
  border: 1px solid lightgrey;
  border-radius: 10px; }

.poison {
  background-color: purple;
  color: white; }

.grass {
  background-color: #06ca06;
  color: white; }

.fire {
  background-color: red;
  color: white; }

.water {
  background-color: blue;
  color: white; }

.flying {
  background-color: lightblue;
  color: black; }

.bug {
  background-color: darkgreen;
  color: white; }

.electric {
  background-color: yellow;
  color: black; }

.evolution {
  margin: 5px;
  border-left: 5px solid brown; }

.evolutionEmpty {
  height: 51px; }

.pokemonEvolutionTitle {
  margin: 5px;
  font-weight: 400; }

.pokemonEvolution {
  margin: 5px; }

.pokemonDataContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: yellow;
  padding-top: 3%; }

.pokemonCardDetail {
  background-color: white;
  width: 90%;
  max-width: 700px;
  box-shadow: 5px 5px 5px #888888; }

.pokeNameContainer {
  background-color: green;
  color: white;
  text-align: center; }

.titlePokemon {
  margin: 0;
  padding: 30px;
  text-transform: capitalize; }

.photoAndDataContainer {
  display: flex;
  flex-direction: row;
  background-color: white; }

.photoAndTypeContainer {
  display: flex;
  flex-direction: column;
  width: 40%; }

.photoContainerDetail {
  text-align: center;
  padding-top: 60px; }

.typeContainerDetail {
  text-align: center; }

.listTypesDetails {
  padding: 0; }

.typeDetails {
  text-transform: uppercase;
  font-weight: 700;
  display: inline-block;
  width: 70px;
  padding: 5px;
  border-radius: 5px;
  margin: 5px;
  font-size: 12px; }

.allDataContainer {
  width: 70%; }

.dataTitleDetail {
  background-color: green;
  padding: 10px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  color: white;
  font-size: 18px; }

.listCharacteristicsDetails {
  list-style: none;
  padding: 0;
  margin-left: 20px; }

.characteristicTitle {
  font-weight: 700; }

.listAbilitiesDetail {
  padding: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap; }

.abilityDetail {
  background-color: #ff7e05;
  padding: 10px;
  font-size: 12px;
  font-weight: 700;
  color: white;
  border-radius: 5px;
  text-transform: uppercase;
  margin-right: 30px; }

.bigTitle {
  background-color: green;
  color: white;
  text-align: center;
  padding: 10px;
  font-size: 18px; }

.listEvolutionsDetail {
  padding: 0px 30px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap; }

.evolutionDetail {
  background-color: black;
  color: white;
  text-transform: uppercase;
  padding: 10px;
  font-weight: 700;
  border-radius: 5px;
  margin: 5px; }

.imagesContainerDetail {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0; }

img {
  transform: scale(1.5);
  margin: 5px; }

.containerButtonBackDetail {
  display: flex;
  justify-content: center; }

.buttonBack {
  background-color: orangered;
  color: white;
  box-shadow: 0;
  border-radius: 5px;
  margin-top: 20px;
  margin-bottom: 50px;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 700;
  padding: 10px; }

.pokeTransition-appear {
  opacity: 0.01; }

.pokeTransition-appear.pokeTransition-appear-active {
  opacity: 1;
  transition: opacity 1s ease-in; }

.pokeTitleTransition-appear {
  width: 0%; }

.pokeTitleTransition-appear.pokeTitleTransition-appear-active {
  width: 100%;
  transition: width 1.5s ease-in; }

.pokeImagesTransition-appear {
  opacity: 0.01; }

.pokeImagesTransition-appear.pokeImagesTransition-appear-active {
  opacity: 1;
  transition: opacity 2.5s ease-in; }
